.titleWrapp {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    @media (min-width: 768px) {
        margin-bottom: 16px;
    }
}


.timeBtn {
    background-color: var(--minor-items-background);
    padding: 8px 16px;
    border-radius: 1000px;
    border: none;
    cursor: pointer;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.29;

    @media (min-width: 768px) {
        font-size: 16px;
        line-height: 1.5;
    }
}

.timeBtn:hover,
.timeBtn:focus {
    outline: 1px solid var(--dark-green);
}

.timeBtnDisable {
    background-color: var(--minor-items-background);
    color: #bab5ba;
    padding: 8px 16px;
    border-radius: 1000px;
    border: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.29;

    @media (min-width: 768px) {
        font-size: 16px;
        line-height: 1.5;
    }
}
